<template>
  <v-container>
    <v-layout
      column wrap align-center justify-center
    >
      <v-flex xs12 sm8 md4>
        <v-card class="elevation-12">
          <v-toolbar dark color="light-green darken-2">
            <v-toolbar-title>Edit Employee</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form lazy-validation ref="uForm">
              <v-text-field disabled
                v-model="employeeId"
              ></v-text-field>
              <v-text-field
                v-model="name"
              ></v-text-field>
              <v-text-field
                v-model="dept"
              ></v-text-field>
              <v-text-field
                v-model="position"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-btn flat color="red lighten-1" to="/">back</v-btn>
            <v-spacer />
            <v-btn color="light-green darken-2" round dark large  @click="updateEmployee()"
              >submit
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  import db from "./firebaseInit";
  export default {
    name: "edit-employee",
    data() {
      return {
        employeeId: null,
        name: null,
        dept: null,
        position: null
      };
    },
    beforeRouteEnter(to, from, next) {
      db.collection("employees")
        .where("employeeId", "==", to.params.employeeId)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            next(vm => {
              vm.employeeId = doc.data().employeeId;
              vm.name = doc.data().name;
              vm.dept = doc.data().dept;
              vm.position = doc.data().position;
            });
          });
        });
    },
    watch: {
      $route: "fetchData"
    },
    methods: {
      fetchData() {
        db.collection("employees")
          .where("employeeId", "==", this.$route.params.employeeId)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              this.employeeId = doc.data().employeeId;
              this.name = doc.data().name;
              this.dept = doc.data().dept;
              this.position = doc.data().position;
            });
          });
      },
      updateEmployee () {
        db.collection("employees")
          .where("employeeId", "==", this.$route.params.employeeId)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.update({
                employeeId: this.employeeId,
                name: this.name,
                dept: this.dept,
                position: this.position
              })
              .then(() => {
                this.$router.push({name: 'view-employee',params: {employeeId: this.employeeId}})
              })
            });
          });
      }
    }
  };
</script>


